// 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 
import store from '@/store/index.js';
import { getSearchVideo } from '@/request/api/search.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 默认每次请求数量
      limit: 24,
      // 决定请求返回的数据范围
      offset: 0,
      // 当前页
      currentPage: 1,
      // 零
      zero: 0,
      // 搜索的关键词
      searchKey: '',
      // 接收的视频
      videos: [],
      // 查到的视频数量
      videoCount: 0,
      // html渲染的创作者们
      vHtmlCreators: '',
      // 是否显示v-html嵌入的创作者关键字html标签
      isVHtmlCreator: 0,
      // html渲染的标题
      vHtmlTitle: '',
      // 是否显示v-html嵌入的标题关键字html标签
      isVHtmlTitle: 0
    };
  },

  mounted() {
    // 获取关键词
    this.searchKey = store.state.searchKey; // 搜索视频

    if (sessionStorage.getItem('searchKey')) this.getSearchVideo();
  },

  methods: {
    // 搜索视频
    async getSearchVideo() {
      let res = await getSearchVideo(sessionStorage.getItem('searchKey'), this.limit, this.offset); // console.log('搜索视频', res);

      this.videoCount = res.data.result.videoCount;
      this.videos = res.data.result.videos;
      if (this.videos.length !== 0) this.isLoading = 0; // console.log(this.videos);
    },

    // 换页
    pageChange() {
      this.offset = (this.currentPage - 1) * this.limit; // 重新搜索视频

      this.getSearchVideo();
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 格式化播放时长
    formatDt(time) {
      return this.$formatDuration(time);
    },

    // 判断是否为大众创作者 
    creatorBy(s) {
      // 转为字符串才有 length
      if (s.toString().length > 8) return true;else return false;
    },

    // 创作者关键词高亮显示 参考博客: https://www.cnblogs.com/pengshengguang/p/8059190.html
    activeCreator(string) {
      // 匹配关键字正则 RegExp(要查找的, 'g') g表示全局(global)模式，即模式将被应用于所有字符串，而非在发现第一个匹配项时就立即停止不再查找
      let reg = new RegExp(this.searchKey, 'g'); // v-html内容
      // let replaceString = '<span class="creator creatorKeyWord">' + this.searchKey + '</span>';

      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>'; // 开始替换 replace(正则查找, 替代之后的值)

      string = string.replace(reg, replaceString); // console.log(string);
      // 传入的字符串中没有关键字

      if (string.indexOf(this.searchKey) == -1) {
        // 不展示 v-html
        this.isVHtmlCreator = 0;
        return string;
      } // 传入的字符串中有关键字，利用 v-html嵌入html标签让关键字高亮
      else {
        // 展示 v-html
        this.isVHtmlCreator = 1; // 渲染的标签

        this.vHtmlCreators = string;
        return '';
      }
    },

    // 标题关键词高亮显示
    activeTitle(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlTitle = 0;
        return string;
      } else {
        this.isVHtmlTitle = 1;
        this.vHtmlTitle = string;
        return '';
      }
    },

    // 去往视频播放页
    goVideoPlayer(mvid) {
      // type == 1是 Mv，2是视频
      let type = mvid.length < 10 ? 1 : 2;

      if (type == 1) {
        this.$router.push({
          path: '/VideoPlayer',
          query: {
            mvid: mvid,
            type: 1
          }
        });
      } else {
        this.$router.push({
          path: '/VideoPlayer',
          query: {
            videoid: videoid,
            type: 2
          }
        });
      }
    }

  }
};